import alt from "../alt";

import SiteActions from "../actions/site-actions";

class SiteStore {
  constructor() {
    this.on("error", (err) => {
      console.log(err);
    });

    this.page = null;

    this.bindListeners({
      navigateToPage: SiteActions.navigateToPage
    });
  }

  navigateToPage(pageInfo) {
    this.page = pageInfo.page;
  }
}

module.exports = alt.createStore(SiteStore, "SiteStore");
